import Vue from 'vue'
import Router from 'vue-router'

const CompositionBase = resolve => require(['@/components/CompositionBase'], resolve)
const ParentBase = resolve => require(['@/components/ParentBase'], resolve)
const AccumulateCamps = resolve => require(['@/components/AccumulateCamps'], resolve)
const ContentBase = resolve => require(['@/views/ContentBase'], resolve)
const Home = resolve => require(['@/views/Home'], resolve)

Vue.use(Router)


const routes = [
    {
        path: '/',
        component: Home,
        meta: ['年级选择']
    },
    {
        path: '/contentBase',
        component: ContentBase,
        children: [
            {
                path: '/contentBase',
                component: CompositionBase,
                meta: ['0']
            },
            {
                path: '/contentBase/CompositionBase',
                component: CompositionBase,
                meta: ['0']
            },
            {
                path: '/contentBase/ParentBase',
                component: ParentBase,
                meta: ['1']
            },
            {
                path: '/contentBase/AccumulateCamps',
                component: AccumulateCamps,
                meta: ['2']
            }
        ]
    }

]

export default new Router({
    routes,
})
